import neilModal from '@/components/neil-modal.vue';
import {
    mapState,
    mapMutations
} from 'vuex';
export default {
	components: {
		neilModal
	},
	data() {
		return {
			inputShow: false,
			feild: undefined,
			inputContent: '',
			genderShow: false,
			gender: undefined,
			genders: [{name: '保密', value:0 },{name: '男', value: 1}, {name: '女', value: 2}]
		};
	},
	computed:{
		...mapState(['userInfo']),
	},
	methods: {
		cancel() {
			this.inputShow = false
			this.genderShow = false
		},
		inputShowModal(feild) {
			this.feild = feild
			this.inputShow = true
			this.inputContent = ''
		},
		genderShowModal() {
			this.genderShow = true
			this.gender = undefined
		},
		confirm() {
			const that = this
			if (!that.inputContent) {
				that.$api.msg('输入不能为空')
				return
			}
			let obj = {}
			obj[that.feild] = that.inputContent
			that.$api.request('user', 'syncUserInfo', obj).then(res => {
				that.userInfo[that.feild] = that.inputContent
				that.inputContent = ''
				that.$store.commit('login', that.userInfo)
			})
		},
		genderRadioChange(e) {
			this.gender = parseInt(e.detail.value)
		},
		confirmGender() {
			const that = this
			if (that.gender === undefined) {
				that.$api.msg('请选择性别')
				return
			}
			let obj = {
				gender: that.gender
			}
			that.$api.request('user', 'syncUserInfo', obj).then(res => {
				that.userInfo.gender = that.gender
			})
		}

	}
}
